import React from 'react';
import { Component } from 'react';
import {connect} from 'react-redux';
import { fetchContactRequests } from '../../redux/actions/ContactsActions';
import ContactList from '../List/index'
import Navbar from '../Navbar';
import Sidebar from '../Sidebar';
import Footer from '../Footer';

class Dashboard extends Component {

    //*delay component render until after dom loads
    
    componentDidMount(){
        this.props.fetchContactRequests()
    }


    render() {
        return(
            <div>
                <Navbar />
                <Sidebar />
                <ContactList />
                <Footer />
            </div>
        )
    }
}

export default connect(null, {fetchContactRequests})(Dashboard)